import Vue from 'vue'
import './plugins/vuetify'
import App from './App.vue'
import router from './router'
import store from './store/index'
import * as fb from 'firebase'
import BuyModalComponent from '@/components/Shared/BuyModal'

Vue.config.productionTip = false


Vue.component('app-buy-modal', BuyModalComponent)

new Vue({
	router,
	store,
	render: h => h(App),
	created() {
		fb.initializeApp({
			apiKey: "AIzaSyBnYPy9ZSbi1O8r847UkpOvzFkEm_eCr0Q",
			authDomain: "itc-ad-project-7a54c.firebaseapp.com",
			databaseURL: "https://itc-ad-project-7a54c.firebaseio.com",
			projectId: "itc-ad-project-7a54c",
			storageBucket: "itc-ad-project-7a54c.appspot.com",
			messagingSenderId: "718884955747",
			appId: "1:718884955747:web:335d1e12096e9067"
		});

		fb.auth().onAuthStateChanged(user => {
			if(user){
				this.$store.dispatch('autoLoginUser', user);
			}
		})

		this.$store.dispatch('fetchAds')
	}
}).$mount('#app')
