import * as fb from 'firebase'

class Ad {
	constructor(title, description, ownerId, imageSrc = 'https://cdn-images-1.medium.com/max/1200/1*-PlqbnwqjqJi_EVmrhmuDQ.jpeg', promo = false, id = null) {
		this.title = title,
			this.description = description,
			this.ownerId = ownerId,
			this.imageSrc = imageSrc,
			this.promo = promo,
			this.id = id
	}
}

export default {
	state: {
		ads: []
	},
	mutations: {
		createAd(state, payload) {
			state.ads.push(payload)
		},
		loadAds(state, payload) {
			state.ads = payload;
		},
		updateAd(state, { title, description, id }){
			const ad = state.ads.find( a => String(a.id) === String(id))

			ad.title = title;
			ad.description = description;
		}
	},
	actions: {
		async createAd({ commit, getters }, payload) {
			commit('clearError')
			commit('setLoading', true)

			const image = payload.image

			try {
				const newAd = new Ad(
					payload.title,
					payload.description,
					getters.user.id,	//pass owner id
					payload.imageSrc,
					payload.promo
				)
				const ad = await fb.database().ref('ads').push(newAd);
				const imageExt = image.name.slice(image.name.lastIndexOf('.'));

				const fileData = await fb.storage().ref(`ads/${ad.key}.${imageExt}`).put(image);

				const imageSrc = await fileData.ref.getDownloadURL()

				await fb.database().ref('ads').child(ad.key).update({
					imageSrc
				})

				commit('setLoading', false)
				let setAd = {
					...newAd,
					id: ad.key,
					imageSrc
				};
				commit('createAd', setAd)


			} catch (error) {
				commit('setError', error.message)
				commit('setLoading', false)
				throw error
			}
		},
		async fetchAds({ commit }) {
			commit('clearError')
			commit('setLoading', true)
			try {
				const fbVal = await fb.database().ref('ads').once('value');
				const ads = fbVal.val()

				const resultAds = Array();

				Object.keys(ads).forEach(key => {
					const ad = ads[key]
					resultAds.push(
						new Ad(
							ad.title,
							ad.description,
							ad.ownerId,
							ad.imageSrc,
							ad.promo,
							key
						)
					)
				});

				commit('loadAds', resultAds);

				commit('setLoading', false)
			} catch (error) {
				commit('setError', error.message)
				commit('setLoading', false)
			}
		},
		async updateAd({ commit }, { title, description, id }) {
			commit('clearError');
			commit('setLoading', true);
			try {
				commit('setLoading', false);

				fb.database().ref('ads').child(id).update({title, description});
				commit('updateAd', { title, description, id });
			} catch (error) {
				commit('setError', error.message)
				commit('setLoading', false);

				throw error
			}


		}
	},
	getters: {
		ads(state) {
			return state.ads
		},
		promoAds(state) {
			return state.ads.filter(ad => ad.promo);
		},
		myAds(state, getters) {
			return state.ads.filter(ad => String(ad.ownerId) === String(getters.user.id))
		},
		adById(state) {
			return id => state.ads.find(ad => String(ad.id) === String(id))
		}
	}
}

