<template>
  <v-dialog v-model="modal" width="400px">
    <v-btn flat class="warning mr-2" slot="activator">Edit</v-btn>

    <v-card>
      <v-container>
        <v-layout row>
          <v-flex xs12>
            <v-card-title>
              <h1 class="text--secondary">Edit Ad</h1>
            </v-card-title>
          </v-flex>
        </v-layout>

        <v-divider></v-divider>

        <v-layout row>
          <v-flex xs12>
            <v-text-field name="title" label="Title" type="text" v-model="editedTitle"></v-text-field>

            <v-textarea
              name="description"
              label="Description"
              type="text"
              v-model="editedDescription"
              multi-line
            ></v-textarea>
          </v-flex>
        </v-layout>

        <v-divider></v-divider>

        <v-layout row>
          <v-flex xs12>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn @click="onCancel" flat>Cancel</v-btn>
              <v-btn @click="onSave" flat class="success">Save</v-btn>
            </v-card-actions>
          </v-flex>
        </v-layout>
      </v-container>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  props: ["ad"],
  data() {
    return {
      modal: false,
      editedTitle: this.ad.title,
      editedDescription: this.ad.description
    };
  },
  methods: {
    onCancel() {
      this.editedDescription = this.ad.description;
      this.editedTitle = this.ad.title;
      this.modal = false;
    },
    onSave() {
      if (this.editedDescription !== "" && this.editedTitle !== "") {
        this.$store.dispatch("updateAd", {
          title: this.editedTitle,
          description: this.editedDescription,
          id: this.ad.id
        });

        this.modal = false;
      }
    }
  }
};
</script>


