<template>
  <v-container>
    <v-layout row>
      <v-flex xs12 sm6 offset-sm3>
        <h1 class="text--secondary">Create new ad</h1>
        <v-form ref="form" v-model="valid" validation class="mb-4">
          <v-text-field
            name="title"
            label="Ad title"
            type="text"
            required
            :rules="[ v => !!v || 'Title is required' ]"
            v-model="title"
          ></v-text-field>

          <v-textarea
            name="description"
            label="Description"
            :rules="[ v => !!v || 'Description is required' ]"
            v-model="description"
          ></v-textarea>
        </v-form>

        <v-layout row mb-3>
          <v-flex xs12>
            <v-btn class="warning" @click="triggerUpload">
              Upload
              <v-icon right dark>cloud_upload</v-icon>
            </v-btn>
            <input
              ref="fileInput"
              type="file"
              style="display: none"
              accept="image/*"
              @change="onFileChange"
            >
          </v-flex>
        </v-layout>

        <v-layout row>
          <v-flex xs12>
            <v-img v-if="imageSrc" :src="imageSrc"></v-img>
          </v-flex>
        </v-layout>

        <v-layout row>
          <v-flex xs12>
            <v-switch v-model="promo" color="primary" :label="'Add to promo'"></v-switch>
          </v-flex>
        </v-layout>
        <v-layout row>
          <v-flex xs12>
            <v-spacer>
              <v-btn
                :disabled="(!valid && Boolean(image) ) || loading"
                :loading="loading"
                class="success"
                @click="createAd"
              >Create add</v-btn>
            </v-spacer>
          </v-flex>
        </v-layout>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      title: "",
      description: "",
      valid: false,
      promo: false,
      image: null,
      imageSrc: ""
    };
  },
  computed: {
    loading() {
      return this.$store.getters.loading;
    }
  },
  methods: {
    onFileChange(event) {
      const file = event.target.files[0];
      const reader = new FileReader();
      reader.onload = () => {
        this.imageSrc = reader.result;
      };
      reader.readAsDataURL(file);
      this.image = file;
    },
    triggerUpload() {
      this.$refs.fileInput.click();
    },
    createAd() {
      if (this.$refs.form.validate() && this.image) {
        const ad = {
          title: this.title,
          description: this.description,
          promo: this.promo,
					imageSrc: "",
					image: this.image
        };
        this.$store.dispatch("createAd", ad).then(() => {
          this.$router.push("/list");
        });
      }
    }
  }
};
</script>

<style scrope>
.upload-image img {
  max-width: 100%;
  max-height: 150px;
}
</style>