import * as fb from 'firebase'

class User {
	constructor(id) {
		this.id = id;
	}
}



export default {
	state: {
		user: null
	},
	mutations: {
		setUser(state, payload) {
			state.user = payload;
		}
	},
	actions: {
		async registerUser({ commit }, { email, password }) {
			commit('clearError')
			commit('setLoading', true)
			try {
				const userData = await fb.auth().createUserWithEmailAndPassword(email, password)
				console.log('userData.user', userData.user)
				console.log('userData.user.uid', userData.user.uid)
				console.log('userData', userData)
				commit('setUser', new User(userData.user.uid))
				commit('setLoading', false)
				console.log('registered success!')
			} catch (error) {
				commit('setLoading', false)
				commit('setError', error.message)
				console.log('error in registeUser', error.message)
				throw error;
			}
		},
		async loginUser({ commit }, { email, password }) {
			commit('clearError')
			commit('setLoading', true)
			try {
				const userData = await fb.auth().signInWithEmailAndPassword(email, password)
				commit('setUser', new User(userData.user.uid))
				commit('setLoading', false)
				console.log('login success!')
			} catch (error) {
				commit('setLoading', false)
				commit('setError', error.message)
				console.log('error in loginUser', error.message)
				throw error;
			}
		},
		logoutUser({commit}){
			fb.auth().signOut()
			commit('setUser', null)
		},
		autoLoginUser({commit}, payload){
			commit('setUser', new User(payload.uid));
		}
	},
	getters: {
		user(state) {
			return state.user
		},
		isUserLoggedIn(state) {
			return state.user !== null
		}
	}
}