<template>
  <v-container>
    <v-layout v-if="!loading && myAds.length !== 0" row>
      <v-flex xs12 sm6 offset-sm3>
        <h1 class="text--secondary mb-3">Ad List</h1>
        <v-card v-for="ad in myAds" :key="ad.id" class="evalation-10 mb-3">
          <v-layout row>
            <v-flex xs4>
              <v-img :src="ad.imageSrc" height="160px"></v-img>
            </v-flex>
            <v-flex xs8>
              <v-card-text>
                <h2 class="text--seconday">{{ad.title}}</h2>
                <p>{{ad.description}}</p>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn :to="'/ad/' + ad.id" class="primary">Open</v-btn>
              </v-card-actions>
            </v-flex>
          </v-layout>
        </v-card>
      </v-flex>
    </v-layout>
		<v-layout v-else-if="!loading && myAds.length === 0">
			<v-flex xs12 class="text-xs-center">
				<h1 class="text--primary">You have no ads</h1>
			</v-flex>
		</v-layout>
		<v-layout v-else>
			<v-flex xs12 class="text-xs-center">
				<v-progress-circular :size="100" :width="3" color="purple" indeterminate></v-progress-circular>
			</v-flex>
		</v-layout>
		
  </v-container>
</template>

<script>
export default {
  data() {
    return {};
  },
  computed: {
    myAds() {
      return this.$store.getters.myAds;
		},
		loading(){
			return this.$store.getters.loading
		}
  }
};
</script>