<template>
  <v-container>
    <v-layout row v-if="!loading">
      <v-flex xs12>
        <v-card>
          <v-img :src="ad.imageSrc" height="300px"></v-img>
          <v-card-text>
            <h1 class="text--secondary">{{ad.title}}</h1>
            <p>{{ad.description}}</p>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>

            <app-edit-ad-modal :ad="ad" v-if="isOwner" ></app-edit-ad-modal>
            <app-buy-modal :ad="ad"></app-buy-modal>
          </v-card-actions>
        </v-card>
      </v-flex>
    </v-layout>
    <v-layout v-else row class="pt-5">
      <v-flex xs12 class="text-xs-center">
        <v-progress-circular :size="100" :width="3" color="purple" indeterminate></v-progress-circular>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import EditAdModal from "./EditAdModal";
export default {
  props: ["id"],
  computed: {
    ad() {
      const id = this.id;
      return this.$store.getters.adById(id);
    },
    loading() {
      return this.$store.getters.loading;
		},
		isOwner(){
			return String(this.ad.ownerId) === String(this.$store.getters.user.id);
		}
  },
  components: {
    appEditAdModal: EditAdModal
  }
};
</script>